sB,EAAA;;AAG1B;EACI,kBAAkB;EAClB,uCAAuC;EACvC,cAAc;EACd,mBAAmB;EACnB,gBAAgB;EAChB,uBAAuB;EACvB,qBdVgB;EcWhB,gBdrBa;EcsBb,iBAAiB;EACjB,gCAAgC;EAChC,qBAAqB;EACrB,iBAAiB;EACjB,YAAY;EACZ,4CAA4C;EAC5C,6BAA6B,EAAA;EAfjC;IAkBQ,WAAW;IACX,kBAAkB;IAClB,SAAS;IACT,UAAU;IACV,cAAc;IACd,UAAU;IACV,WAAW;IACX,kBAAkB;IAClB,qBd7BY,EAAA","file":"main.scss","sourcesContent":["@import 'theme-default';\n$gui-z-index:1000;\n$gui-size-unit:45px;\n$gui-size-unit-sp: 35px;\n\n$anm-short: 0.2s ease-in-out;\n$anm-middle: 0.4s ease-in-out;\n$anm-long: 0.8s ease-in-out;\n\n*,\n*:before,\n*:after {\n    -webkit-box-sizing: border-box;\n    -moz-box-sizing: border-box;\n    -o-box-sizing: border-box;\n    -ms-box-sizing: border-box;\n    box-sizing: border-box;\n}\n\n.noselect {\n    -webkit-touch-callout: none;\n    /* iOS Safari */\n    -webkit-user-select: none;\n    /* Safari */\n    -khtml-user-select: none;\n    /* Konqueror HTML */\n    -moz-user-select: none;\n    /* Firefox */\n    -ms-user-select: none;\n    /* Internet Explorer/Edge */\n    user-select: none;\n    /* Non-prefixed version, currently supported by Chrome and Opera */\n}\n\n$breakpoint: 735px;\n\n@mixin mq {\n    @media screen and (max-width: $breakpoint) {\n        @content;\n    }\n}","@import 'base';\n\n.container {\n    z-index: ($gui-z-index)+1;\n    background: $col_bg;\n    position: absolute;\n    margin: 0px;\n    top: 0px;\n    left: 0px;\n    width: 100%;\n    height: 100%;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    align-content: center;\n\n    @include mq {\n        background: $col_bg-sp;\n    }\n}","$col_frnt:#d8d4c5FF;\n$col-frnt-bright: #efebdcFF;\n$col-frnt-dark: #6f6b5cFF;\n$col-frnt-bright-lighten: lighten($col-frnt-bright, 4%);\n$col-frnt-bright-darken: darken($col-frnt-bright, 4%);\n$col-frnt-dark-lighten: lighten($col-frnt-dark, 4%);\n$col-frnt-dark-darken: darken($col-frnt-dark, 4%);\n\n$col_bg:#1F1F1FFF;\n$col-bg-bright: #2C2C2CFF;\n$col-bg-dark: #1A1A1AFF;\n$col-bg-bright-lighten: lighten($col-bg-bright, 16%);\n$col-bg-bright-darken: darken($col-bg-bright, 3%);\n$col-bg-dark-lighten: lighten($col-bg-dark, 4%);\n$col-bg-dark-darken: darken($col-bg-dark, 4%);\n\n$col_bg-sp:#1F1F1FDD;\n\n$col-error:#FE8565FF;\n$col-suc1:#5acb65FF;\n$col-suc2:#609cdfFF;\n$col-suc3:#e1c169FF;\n$col-record:#e23333FF;","@import 'base';\n\n#main {\n    position: relative;\n    width: 100vw;\n    height: 100vh;\n    overflow: hidden;\n    display: block;\n}\n\n.gui {\n    color: $col_frnt;\n}\n\n.gui-layer {\n    z-index: $gui-z-index;\n}\n\n#gui-panel {\n    z-index: $gui-z-index*2;\n}\n\n.code-container {\n    position: absolute;\n    top: 0px;\n    left: 0px;\n    display: flex;\n    overflow: hidden;\n\n    width: 100vw;\n\n    transition: all $anm-middle;\n\n    transform: translate(0, 0);\n\n    @include mq {\n        top: 0px;\n    }\n}\n\n.code_hide {\n    width: 0vw;\n}\n\n.code-container-mov-l {\n    transform-origin: center top;\n    animation: anm-code-container-mov-l 0.2s both;\n    animation-timing-function: ease-in-out;\n    animation-delay: 0.0s;\n}\n\n.code-container-mov-r {\n    transform-origin: center top;\n    animation: anm-code-container-mov-r 0.2s both;\n    animation-timing-function: ease-in-out;\n    animation-delay: 0.0s;\n}\n\n.code-container-hidden {\n    transform-origin: left top;\n    flex: 0 0 0;\n    width: 0%;\n}\n\n@keyframes anm-code-container-mov-l {\n    0% {\n        opacity: 1;\n        // transform: translate($gui-size-unit*-0.25, 0);\n    }\n\n    100% {\n        opacity: 1;\n        // transform: translate(0, 0);\n    }\n}\n\n@keyframes anm-code-container-mov-r {\n    0% {\n        opacity: 1;\n        // transform: translate($gui-size-unit*-0.25, 0);\n    }\n\n    100% {\n        opacity: 1;\n        // transform: translate(0, 0);\n    }\n}\n\n.editor {\n    text-align: left;\n    width: 0px;\n    height: auto;\n    flex: 1;\n    margin: 0px;\n}","@import 'base';\n\n#div_authrise {\n    position: absolute;\n    top: 0px;\n    left: 0px;\n    width: 100vw;\n    height: 100vh;\n    margin: 0px;\n    background: $col_bg;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n\n    opacity: 1;\n\n    transition: all $anm-middle;\n\n    &.hide {\n        height: 0px;\n        opacity: 0;\n    }\n\n    &.hidden {\n        display: none;\n    }\n}\n\n.auth-content {\n    background: $col_bg;\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    align-items: center;\n    margin-bottom: 10px;\n\n    margin-top: 0px;\n    opacity: 1;\n\n    transition: all $anm-long;\n\n    &.hide {\n        margin-top: 50px;\n        opacity: 0;\n    }\n}\n\n.glogo {\n    fill: $col_frnt;\n    width: 300px;\n\n    @include mq {}\n}\n\n.gdiscription {\n    text-align: left;\n    letter-spacing: 0.015em;\n    line-height: 1.5;\n    width: 300px;\n    font-size: 12.5px;\n    margin-bottom: $gui-size-unit*0.5;\n\n    @include mq {\n        letter-spacing: 0.03vw;\n        line-height: 4vw;\n        width: 59vw;\n        height: 10vw;\n        font-size: 2.75vw;\n    }\n}\n\n.btns-container {\n    display: inline-flex;\n    justify-content: space-between;\n    align-items: center;\n    width: 300px;\n    height: $gui-size-unit;\n}\n\n.btn_authrise {\n    display: inline-flex;\n    justify-content: center;\n    align-items: center;\n\n    color: $col_frnt;\n    background: $col_bg-bright;\n    height: $gui-size-unit;\n    width: 149.5px;\n\n    transition: all $anm-short;\n\n    &>span {\n        font-size: 14px;\n        font-weight: 700;\n        letter-spacing: 0.05em;\n        margin-bottom: 3px;\n    }\n\n    &:hover {\n        cursor: pointer;\n        background: $col_bg-bright-darken;\n    }\n\n    @include mq {}\n}\n\n#btn_authrise_later {\n    &>span {\n        font-weight: 400;\n        margin-bottom: 1px;\n    }\n}","@import 'base';\n\n#ctrl {\n    position: absolute;\n    top: 0px;\n    right: 0px;\n    overflow: hidden;\n    width: $gui-size-unit*5;\n    height: 100vh;\n\n    transition: width $anm-short;\n\n    &.ctrl_hide {\n        width: 0px;\n    }\n}\n\n.gui-panel-knob {\n    z-index: $gui-z-index;\n    padding: $gui-size-unit;\n    padding-top: $gui-size-unit*2;\n    width: $gui-size-unit*5;\n    height: 100%;\n    background: $col_bg;\n    position: absolute;\n    top: 0px;\n    right: 0px;\n\n    display: flex;\n    flex-wrap: wrap;\n    justify-content: flex-start;\n    align-content: flex-start;\n}\n\n.gui-knob {\n    z-index: $gui-z-index;\n    text-align: center;\n    padding: 0px; // margin: 0.5px;\n    width: $gui-size-unit;\n    height: $gui-size-unit;\n    font-size: 12px;\n    margin: 0px;\n    background: $col_bg;\n    // border: $col_bg 1px solid;\n    // border-radius: 3px;\n    transition: all $anm-middle;\n\n    @include mq {\n        background: $col_bg-sp;\n        border: $col_bg-sp 1px solid;\n    }\n}\n\n.knob-name-col {\n    position: absolute;\n    top: $gui-size-unit*1;\n    left: $gui-size-unit*0.5;\n    font-family: \"Muli\", sans-serif;\n    font-size: 0.6em;\n    line-height: $gui-size-unit;\n    color: #555555;\n}\n\n.knob-name-raw {\n    position: absolute;\n    width: $gui-size-unit*3;\n    height: $gui-size-unit;\n    top: 0px;\n    left: $gui-size-unit;\n\n    &>span {\n        display: inline-block;\n        padding-top: $gui-size-unit*0.5;\n        width: $gui-size-unit;\n        height: $gui-size-unit;\n        margin: 0px;\n        font-family: \"Muli\", sans-serif;\n        font-size: 0.6em;\n        color: #555555;\n    }\n}\n\n.gui-knob-svg-body {\n    fill: $col_frnt;\n}\n\n.gui-knob-svg-dot {\n    fill: $col_bg;\n\n    @include mq {\n        fill: $col_bg-sp;\n    }\n}\n\n.gui-knob.comp {\n    opacity: 0.2;\n}\n\n.gui-knob.comp.active {\n    background: $col-bg-bright;\n    opacity: 1.0;\n}\n\n.gui-knob.comp:hover {\n    background: $col-bg-bright;\n    opacity: 1.0;\n}\n\n.gui-knob.comp:hover>div>div {\n    opacity: 1.0;\n\n    // &.name,\n    // &.value {\n    //     opacity: 1.0;\n    // }\n}\n\n.gui-knob .name {\n    font-size: 11px;\n    font-weight: 700;\n    margin: 0px;\n    margin-top: 3px;\n    opacity: 0.0;\n}\n\n.gui-knob.comp.active>div+div+div {\n    opacity: 0.5;\n}\n\n.gui-knob.comp.active:hover>div+div+div {\n    opacity: 1.0;\n    transition: all $anm-middle;\n}\n\n.gui-knob.comp.active>div {\n    opacity: 1.0;\n}\n\nsvg {\n    width: 100%;\n    height: 100%;\n}\n\n.knob {\n    transform: rotate(0);\n    width: 12px;\n    height: 12px;\n    margin: 0px;\n    margin-top: 2px;\n    margin-left: $gui-size-unit*0.5 - 6px;\n    background: #00000000;\n    cursor: none;\n    transform-origin: 50% 50%;\n}\n\n.gui-knob-svg-body {\n    fill: $col_frnt;\n}\n\n.gui-knob-svg-dot {\n    fill: $col_bg;\n\n    @include mq {\n        fill: $col_bg-sp;\n    }\n}\n\n.gui-knob .value {\n    font-size: 10px;\n    color: $col_frnt;\n    opacity: 0.0;\n    margin: 0px;\n    margin-top: 1.5px;\n    cursor: none;\n}","@import 'base';\n\n#gp-base {\n    position: absolute;\n    display: flex;\n    flex-wrap: wrap;\n    width: 100vw;\n    height: 100vh;\n    overflow: scroll;\n}\n\n.gp-shader-list {\n    position: absolute;\n    display: flex;\n    flex-wrap: wrap;\n    width: 100vw;\n    height: 100vh;\n    overflow: scroll;\n    padding: 35px;\n\n    &.hide {\n        display: none;\n        height: 0px;\n    }\n\n    @include mq {\n        padding: 27.5px;\n    }\n}\n\n\n.gp-container-appear {\n    // transform-origin: center top;\n    display: flex;\n    animation: anm-gp-container-appear 0.4s both;\n    animation-timing-function: ease-inout;\n    animation-delay: 0.0s;\n}\n\n.gp-appear {\n    // transform-origin: center top;\n    display: flex;\n    animation: anm-gp-appear 0.4s both;\n    animation-timing-function: ease-out;\n    animation-delay: 0.2s;\n}\n\n.gp-container-hide {\n    // transform-origin: center top;\n    display: flex;\n    animation: anm-gp-container-hide 0.8s both;\n    animation-timing-function: ease-inout;\n    animation-delay: 0.0s;\n}\n\n.gp-hide {\n    // transform-origin: center top;\n    display: flex;\n    animation: anm-gp-hide 0.8s both;\n    animation-timing-function: ease-out;\n    animation-delay: 0.25s;\n}\n\n.gp-container-hidden {\n    // transform-origin: center top;\n    width: 0%;\n    opacity: 0;\n    display: none;\n}\n\n.gp-hidden {\n    // transform-origin: center top;\n    transform: translate(2em, 0);\n    opacity: 0;\n    display: none;\n}\n\n\n\n@keyframes anm-gp-container-appear {\n    0% {\n        opacity: 0;\n        width: 0%;\n    }\n\n    100% {\n        opacity: 1;\n        width: 100%;\n    }\n}\n\n@keyframes anm-gp-appear {\n    0% {\n        transform: translate(2em, 0);\n        opacity: 0;\n    }\n\n    100% {\n        transform: translate(0, 0);\n        opacity: 1;\n    }\n}\n\n@keyframes anm-gp-container-hide {\n    0% {\n        opacity: 1;\n        width: 100%;\n    }\n\n    100% {\n        opacity: 0;\n        width: 0%;\n    }\n}\n\n@keyframes anm-gp-hide {\n    0% {\n        transform: translate(0, 0);\n        opacity: 1;\n    }\n\n    100% {\n        transform: translate(0, 0);\n        opacity: 0;\n    }\n}","@import 'gui_panel_base';\n\n// main\n///////////////////////////////////////////////////\n\n%btn__base {\n    position: absolute;\n    align-items: center;\n    display: flex;\n    justify-content: center;\n    font-size: 0.8rem;\n    // transition: all 1.5s ease-out;\n    border-radius: 0px 0px 0px 0px;\n    // height: 100%;\n    // width: calc((100vw - 110px)/4);\n}\n\n.btn {\n    // transform-origin: center top;\n    width: calc((100vw - 110px)/4);\n    height: calc(((100vw - 110px)/4) * 0.56 + 45px);\n\n    @include mq {\n        width: calc((100vw - 85px)/3);\n        height: calc(((100vw - 85px)/3) * 0.56 + 35px);\n    }\n\n    margin: 5px;\n    overflow: hidden;\n    border-radius: 0px 0px;\n    background: #0000FF01;\n\n    font-family: 'Muli',\n    sans-serif;\n    font-weight: 700;\n  