   @include mq {\n            // width: calc((100vw - 85px)/3);\n            height: $gui-size-unit-sp;\n            margin-top: calc(((100vw - 85px)/3) * 0.56);\n        }\n\n        transition: all $anm-middle;\n        z-index: 2;\n        width: 0px;\n\n        span {\n            align-items: center;\n            display: flex;\n            justify-content: center;\n            position: absolute;\n            top: -1px;\n            left: 0;\n            right: auto;\n            height: 100%;\n            width: calc((100vw - 110px)/4);\n\n            @include mq {\n                width: calc((100vw - 85px)/3);\n            }\n        }\n    }\n\n    &.init {\n        .btn__base {\n            opacity: 0;\n        }\n    }\n\n    &.hover {\n        .btn-thumb {\n            background-size: 115% 115%;\n        }\n\n        .btn__base {\n\n            width: 0%;\n            transition: all $anm-short;\n        }\n\n        .btn__hover {\n            right: auto;\n            left: 10;\n            width: calc((100vw - 110px)/4);\n\n            @include mq {\n                width: calc((100vw - 85px)/3);\n            }\n\n            transition: all $anm-short;\n\n            span {\n                left: 0;\n                right: auto;\n            }\n\n            &:active {\n                color: $col-bg-bright;\n                background: $col-frnt-bright;\n            }\n        }\n    }\n\n    &.active {\n        .btn__base {\n            transition: all $anm-middle;\n            z-index: 2;\n            width: 0%;\n\n            span {\n                align-items: center;\n                display: flex;\n                justify-content: center;\n                position: absolute;\n                left: 0;\n                right: auto;\n                height: 100%;\n                width: calc((100vw - 110px)/4);\n\n                @include mq {\n                    width: calc((100vw - 85px)/3);\n                }\n            }\n        }\n\n  